/* ==========================================================================
   Syntax highlighting
   ========================================================================== */

div.highlighter-rouge,
figure.highlight {
    position: relative;
    margin-bottom: 1em;
    background: $base00;
    color: $base05;
    font-family: $monospace;
    font-size: $type-size-6;
    line-height: 1.8;
    border-radius: $border-radius;

    > pre,
    pre.highlight {
        margin: 0;
        padding: 1em;
    }
}

.highlight table {
    margin-bottom: 0;
    font-size: 1em;
    border: 0;

    td {
        padding: 0;
        width: calc(100% - 1em);
        border: 0;

        /* line numbers*/
        &.gutter,
        &.rouge-gutter {
            padding-right: 1em;
            width: 1em;
            color: $base04;
            border-right: 1px solid $base04;
            text-align: right;
        }

        /* code */
        &.code,
        &.rouge-code {
            padding-left: 1em;
        }
    }

    pre {
        margin: 0;
    }
}

.highlight pre {
    width: 100%;
}

.highlight .hll {
    background-color: $base06;
}
.highlight {
    .c {
        /* Comment */
        color: $base04;
    }
    .err {
        /* Error */
        color: $base08;
    }
    .k {
        /* Keyword */
        color: $base0e;
    }
    .l {
        /* Literal */
        color: $base09;
    }
    .n {
        /* Name */
        color: $base05;
    }
    .o {
        /* Operator */
        color: $base0c;
    }
    .p {
        /* Punctuation */
        color: $base05;
    }
    .cm {
        /* Comment.Multiline */
        color: $base04;
    }
    .cp {
        /* Comment.Preproc */
        color: $base04;
    }
    .c1 {
        /* Comment.Single */
        color: $base04;
    }
    .cs {
        /* Comment.Special */
        color: $base04;
    }
    .gd {
        /* Generic.Deleted */
        color: $base08;
    }
    .ge {
        /* Generic.Emph */
        font-style: italic;
    }
    .gh {
        /* Generic.Heading */
        color: $base05;
        font-weight: bold;
    }
    .gi {
        /* Generic.Inserted */
        color: $base0b;
    }
    .gp {
        /* Generic.Prompt */
        color: $base04;
        font-weight: bold;
    }
    .gs {
        /* Generic.Strong */
        font-weight: bold;
    }
    .gu {
        /* Generic.Subheading */
        color: $base0c;
        font-weight: bold;
    }
    .kc {
        /* Keyword.Constant */
        color: $base0e;
    }
    .kd {
        /* Keyword.Declaration */
        color: $base0e;
    }
    .kn {
        /* Keyword.Namespace */
