    }
    .ld {
        /* Literal.Date */
        color: $base0b;
    }
    .m {
        /* Literal.Number */
        color: $base09;
    }
    .s {
        /* Literal.String */
        color: $base0b;
    }
    .na {
        /* Name.Attribute */
