/* ==========================================================================
   UTILITY CLASSES
   ========================================================================== */

/*
   Visibility
   ========================================================================== */

/* http://www.456bereastreet.com/archive/200711/screen_readers_sometimes_ignore_displaynone/ */

.hidden,
.is--hidden {
    display: none;
    visibility: hidden;
}

/* for preloading images */

.load {
    display: none;
}

.transparent {
    opacity: 0;
}

/* https://developer.yahoo.com/blogs/ydn/clip-hidden-content-better-accessibility-53456.html */

.visually-hidden,
.screen-reader-text,
.screen-reader-text span,
.screen-reader-shortcut {
    position: absolute !important;
    clip: rect(1px, 1px, 1px, 1px);
    height: 1px !important;
    width: 1px !important;
    border: 0 !important;
    overflow: hidden;
}

body:hover .visually-hidden a,
body:hover .visually-hidden input,
body:hover .visually-hidden button {
    display: none !important;
}

/* screen readers */

.screen-reader-text:focus,
.screen-reader-shortcut:focus {
    clip: auto !important;
    height: auto !important;
    width: auto !important;
    display: block;
    font-size: 1em;
    font-weight: bold;
    padding: 15px 23px 14px;
    background: #fff;
    z-index: 100000;
    text-decoration: none;
    box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
}

/*
   Skip links
   ========================================================================== */

.skip-link {
    position: fixed;
    z-index: 20;
    margin: 0;
    font-family: $sans-serif;
    white-space: nowrap;
}

.skip-link li {
    height: 0;
    width: 0;
    list-style: none;
}

/*
   Type
   ========================================================================== */

.text-left {
    text-align: left;
}

.text-center {
    text-align: center;
}

.text-right {
    text-align: right;
}

.text-justify {
    text-align: justify;
}

.text-nowrap {
    white-space: nowrap;
}

/*
   Task lists
   ========================================================================== */

.task-list {
    padding: 0;

    li {
        list-style-type: none;
    }

    .task-list-item-checkbox {
        margin-right: 0.5em;
        opacity: 1;
    }
}

.task-list .task-list {
    margin-left: 1em;
}

/*
   Alignment
   ========================================================================== */

/* clearfix */

.cf {
    clear: both;
}

.wrapper {
    margin-left: auto;
    margin-right: auto;
    width: 100%;
}

/*
   Images
   ========================================================================== */

/* image align left */

.align-left {
    display: block;
    margin-left: auto;
    margin-right: auto;

    @include breakpoint($small) {
        float: left;
        margin-right: 1em;
    }
}

/* image align right */

.align-right {
    display: block;
    margin-left: auto;
    margin-right: auto;

    @include breakpoint($small) {
        float: right;
        margin-left: 1em;
    }
}

/* image align center */

.align-center {
    display: block;
    margin-left: auto;
    margin-right: auto;
}

/* file page content container */

.full {
    @include breakpoint($large) {
        margin-right: -1 * span(2.5 of 12) !important;
    }
}

/*
   Icons
   ========================================================================== */

.icon {
    display: inline-block;
    fill: currentColor;
    width: 1em;
    height: 1.1em;
    line-height: 1;
    position: relative;
    top: -0.1em;
    vertical-align: middle;
}

