/*
   Navicons
   ========================================================================== */

.navicon {
    position: relative;
    width: $navicon-width;
    height: $navicon-height;
    background: $primary-color;
    margin: auto;
    -webkit-transition: 0.3s;
    transition: 0.3s;

    &:before,
    &:after {
        content: '';
        position: absolute;
        left: 0;
        width: $navicon-width;
        height: $navicon-height;
        background: $primary-color;
        -webkit-transition: 0.3s;
        transition: 0.3s;
    }

    &:before {
        top: (-2 * $navicon-height);
    }

    &:after {
        bottom: (-2 * $navicon-height);
    }
}

.close .navicon {
    /* hide the middle line*/
    background: transparent;

    /* overlay the lines by setting both their top values to 0*/
    &:before,
    &:after {
        -webkit-transform-origin: 50% 50%;
        -ms-transform-origin: 50% 50%;
        transform-origin: 50% 50%;
        top: 0;
        width: $navicon-width;
    }

    /* rotate the lines to form the x shape*/
    &:before {
        -webkit-transform: rotate3d(0, 0, 1, 45deg);
        transform: rotate3d(0, 0, 1, 45deg);
    }
    &:after {
        -webkit-transform: rotate3d(0, 0, 1, -45deg);
        transform: rotate3d(0, 0, 1, -45deg);
    }
}

.greedy-nav__toggle {
    &:before {
        @supports (pointer-events: none) {
            content: '';
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            opacity: 0;
            background-color: $background-color;
            -webkit-transition: $global-transition;
            transition: $global-transition;
            pointer-events: none;
        }
    }

    &.close {
        &:before {
            opacity: 0.9;
            -webkit-transition: $global-transition;
            transition: $global-transition;
            pointer-events: auto;
        }
    }
}

.greedy-nav__toggle:hover {
    .navicon,
    .navicon:before,
    .navicon:after {
        background: mix(#000, $primary-color, 25%);
    }

    &.close {
        .navicon {
            background: transparent;
        }
    }
}

/*
   Sticky, fixed to top content
   ========================================================================== */

.sticky {
    @include breakpoint($large) {
        @include clearfix();
        position: -webkit-sticky;
        position: sticky;
        top: 2em;
        padding-right: 3;
        margin-right: 3;

        > * {
            display: block;
        }
    }
}

/*
   Wells
   ========================================================================== */

.well {
    min-height: 20px;
    padding: 19px;
    margin-bottom: 20px;
    background-color: #f5f5f5;
    border: 1px solid #e3e3e3;
    border-radius: $border-radius;
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.05);
}

/*
   Modals
   ========================================================================== */

.show-modal {
    overflow: hidden;
    position: relative;

    &:before {
        position: absolute;
        content: '';
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 999;
        background-color: rgba(255, 255, 255, 0.85);
    }

    .modal {
        display: block;
    }
}

.modal {
    display: none;
    position: fixed;
    width: 300px;
    top: 50%;
    left: 50%;
    margin-left: -150px;
    margin-top: -150px;
    min-height: 0;
    z-index: 9999;
    background: #fff;
    border: 1px solid $border-color;
    border-radius: $border-radius;
    box-shadow: $box-shadow;

    &__title {
        margin: 0;
        padding: 0.5em 1em;
    }

    &__supporting-text {
        padding: 0 1em 0.5em 1em;
    }

    &__actions {
        padding: 0.5em 1em;
        border-top: 1px solid $border-color;
    }
}

/*
   Footnotes
   ========================================================================== */

.footnote {
    color: mix(#fff, $gray, 25%);
    text-decoration: none;
}

.footnotes {
    color: mix(#fff, $gray, 25%);

    ol,
    li,
    p {
        margin-bottom: 0;
        font-size: $type-size-6;
    }
}

a.reversefootnote {
    color: $gray;
    text-decoration: none;

    &:hover {
        text-decoration: underline;
    }
}

/*
   Required
   ========================================================================== */

.required {
    color: $danger-color;
    font-weight: bold;
}

/*
   Google Custom Search Engine
   ========================================================================== */

.gsc-control-cse {
    table,
    tr,
    td {
        border: 0; /* remove table borders widget */
    }
}

/*
   Responsive Video Embed
   ========================================================================== */

.responsive-video-container {
    position: relative;
    margin-bottom: 1em;
    padding-bottom: 56.25%;
    height: 0;
    overflow: hidden;
    max-width: 100%;

    iframe,
    object,
    embed {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
}

// full screen video fixes
:-webkit-full-screen-ancestor {
    .masthead,
    .page__footer {
        position: static;
    }
}
